
.swiper-pagination-bullet {
  height: 0.21333rem !important;
  width: 0.21333rem !important;
}
.swiper__box {
  position: relative;
}
.swiper-button {
  position: absolute;
  left: 0.26667rem;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 0.42667rem !important;
  height: 0.85333rem !important;
}
.swiper-button.disabled {
    opacity: 0.5;
}
.swiper-button.swiper-button-next {
    left: auto;
    right: 0.26667rem;
}
.swiper-button-prev, .swiper-container-rtl .swiper-button-next {
  background-image: url(../style/public/icon-swiper-left.svg) !important;
}
.swiper-button-next, .swiper-container-rtl .swiper-button-prev {
  background-image: url(../style/public/icon-swiper-right.svg) !important;
}
.swiper-slide img {
  min-width: 100% !important;
  min-height: 100% !important;
}



